import React from "react";

const AddNew = (props) => {
  return (
    <>
      <form onSubmit={(e) => props.handleSetPersons(e)}>
        <div>
          name:
          <input
            type="text"
            value={props.newName}
            onChange={(e) => props.handleSetName(e)}
            required
          />
        </div>
        <div>
          number:
          <input
            type="text"
            value={props.newPhone}
            onChange={(e) => props.handleSetPhone(e)}
            required
          />
        </div>
        <div>
          <button type="submit">add</button>
        </div>
      </form>
    </>
  );
};

export default AddNew;
